html{
  // overflow: hidden
font-family: Outfit
}
body {
  @include getCssVar(font-family);
  @include getCssVar(background-color, $white);
  font-size: 1rem;
  position: relative;
  height: 100%;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
main{
  height: calc(var(--vh, 1vh) * 100 - var(--nav-height, 0px) - var(--url-height, 0px));
	overflow-y: scroll;
  overflow-x: scroll;
  scroll-snap-type: y mandatory;
}


.menu{
  display: none;
}

*::-webkit-scrollbar {
  display: none
}