.menu-five{
    width: 100%;
    height: 100%;
    background-color: $white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    // padding-inline: 50px;
    gap: 30px;
    footer{
        // align-self:center;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 10vh;
        background-color:darken($color: #333333 , $amount: 5%);
        width:100%;
        text-align: center;
        color: $gray;
        .designed{
            text-decoration: none;
            color: $red;
        }
        p{
            margin: 0;
        }
    }
    .content{
        margin: auto;
        width: 100%;
        height: auto;
        h2{
            color:$red;
            padding-inline: 50px;
           } 
           .beuty-text{
            color: bisque;
            border-block:1px solid $red;
            text-align: center;
            margin-top: 3rem;
           }
           p{
            padding-inline: 50px;
            color:$white;
            max-width: 80%;
           }
           .button{
            text-decoration: none;
                color: $red;
                padding: 10px;
                border: 1px solid $red;
                width: 180px;
                margin-bottom: 2rem;
                display: block;
           }
           .links{
            display: flex;
            justify-content: space-evenly;
            gap: 3rem;
            padding-top: 3rem;
            a{
                text-align: center;
                text-decoration: none;
                color: $red; 
                padding: 10px;
                border: 1px solid $red;
                width: 180px;
                display: flex;
                justify-content: center;
                align-items:center;
                transition: color 0.5s ease-in;
                transition: border 0.5s ease-in;
                svg{
                    fill: $red;
                    transition: fill 0.5s ease-in;
                }
                span{
                    color: $red;
                    transition: color 0.5s ease-in;
                }
               }
               a:hover{
                
                border: 1px solid $white;
                color: $white;
                svg{
                    fill: $white;
                }
                span{
                    color: $white; 
                }
             }
           }
    }
}